import React, { Component } from 'react';
import { HashRouter } from 'react-router-dom';
import Base from "./base";
import { mounting_script } from "../func-lib/load-script";
class Route extends Component {
	componentWillMount( ) {
		mounting_script( )
	}
	render( ) {
		return (
			<HashRouter>
				<Base/>
			</HashRouter>
		)
	}
}
export default Route;