import React, { Component } from 'react';
import { center_container, fullscreen } from "../../style/style";
import { get_gps_location } from "../../func-lib/gps";
import { map_setting } from "../../config/config-map";
import { dummy_user } from "../../dummy/loc-dummy";
import MapCenter from "../Map/map-center-button";
let marker = null;
class Submit extends Component {
	state = {
		user_loc: null,
		selected_loc: dummy_user
	};
	componentDidMount( ) {
		get_gps_location( this.set_position )
	}
	set_position = ( position ) => {
		const pos = {
			lat: position.coords.latitude,
			lng: position.coords.longitude
		};
		this.setState({ user_loc: pos });
		window.map = new window
			.google
			.maps
			.Map(document.getElementById( 'map' ), map_setting( pos ));
		window
			.map
			.addListener('click', ( e ) => {
				if ( marker != null ) {
					this.clearMarker( )
				}
				marker = new window
					.google
					.maps
					.Marker({ position: e.latLng, map: window.map, draggable: true });
				this.placeMarkerAndPanTo( e.latLng, marker, window.map )
			})
	}
	placeMarkerAndPanTo = ( latLng, marker, map ) => {
		marker.addListener('dragend', ( e ) => {
			const pos = {
				lat: marker
					.getPosition( )
					.lat( ),
				lng: marker
					.getPosition( )
					.lng( )
			};
			this.setState({ selected_loc: pos })
		});
		map.panTo( latLng );
		const pos = {
			lat: marker
				.getPosition( )
				.lat( ),
			lng: marker
				.getPosition( )
				.lng( )
		};
		this.setState({ selected_loc: pos })
	}
	clearMarker = ( ) => {
		marker.setMap( null )
	}
	set_map_center = ( ) => {
		window
			.map
			.setCenter( this.state.user_loc )
	}
	render( ) {
		return (
			<div style={center_container}>
				<div style={fullscreen} id='map'/>
				<MapCenter action={this.set_map_center}/>
			</div>
		)
	}
}
export default Submit;